import React, { Component } from 'react'
import { css } from '@emotion/core'
// import { Link } from 'Config/routes'

import { Card, Button, Upload, Icon, Form, Row, Col, Input, Select, DatePicker } from 'antd'

const Option = Select.Option

const formLayout = {
  labelCol: { span: 5 },
  wrapperCol: { span: 18 }
}

const buttonLayout = {
  wrapperCol: {
    span: 24
  }
}

class AddEmployee extends Component {
  render(){
    return(
      <>
      <div><h2>Tambah Karyawan</h2></div>
      <Card css={css`
        border-radius: 8px;
        border-top: 4px solid rgb(24, 144, 255);
        box-shadow: 1px 4px 15px rgba(24, 144, 255, 0.1);
        padding-top: 1em;
      `}>
        <div>
          <Form {...formLayout}>
            <Row gutter={16}>
              <Col xs={{ span: 24 }} lg={{ span: 8 }} css={css`
                display: flex;
                justify-content: center;
              `}>
                <div css={css`
                    height: 200px;
                    width: 200px;
                    border-radius: 50%;
                    position: relative;
                    background-size: cover;
                    background-repeat: no-repeat;
                    background-image: url('/static/defaultplaceholder.png');
                  `} 
                className="photoEmployee"
                >
                  <Upload>
                    <Button css={css`
                      position: absolute;
                      bottom: 0px;
                      right: 45px;
                      border-radius: 50%;
                      height: 40px;
                      width: 40px;
                      display: flex;
                    `}>
                      <Icon css={css`font-size: 16px; justify-self: center;`} type="edit" />
                    </Button>
                  </Upload>
                </div>
              </Col>
              <Col xs={{ span: 24 }} lg={{ span: 16 }}>
                <Form.Item label="Nama Lengkap">
                  <Input />
                </Form.Item>
                <Form.Item label="NIK">
                  <Input />
                </Form.Item>
                <Form.Item label="Payment Type">
                  <Select
                    showSearch
                    placeholder="- Pilih Jenis Kelamin -"
                  >
                    <Option value="pria">Laki-Laki</Option>
                    <Option value="wanita">Perempuan</Option>
                  </Select>
                </Form.Item>
                <Form.Item label="Tempat/Tanggal Lahir">
                  <Row gutter={16}>
                    <Col span={16}>
                      <Input /> 
                    </Col>
                    <Col span={8}>
                      <DatePicker />
                    </Col>
                  </Row>
                </Form.Item>
                <Form.Item label="No. Telepon">
                  <Input />
                </Form.Item>
                <Form.Item label="Jabatan">
                  <Select
                    showSearch
                    placeholder="- Pilih Jabatan -"
                  >
                    <Option value="pria">Bos</Option>
                    <Option value="pria">Super Admin</Option>
                    <Option value="wanita">Staf Kasir</Option>
                  </Select>
                </Form.Item>
                <Form.Item label="Shift">
                  <Select
                    showSearch
                    placeholder="- Pilih Shift -"
                  >
                    <Option value="pria">Shift I (06.00-12.00)</Option>
                    <Option value="wanita">Shift II (12.00-18.00)</Option>
                  </Select>
                </Form.Item>
                <Form.Item {...buttonLayout}>
                  <div css={css`display: flex; justify-content: flex-end; margin-right: 25px;`}>
                    <Button type="danger" css={css`margin-right: 1em;`}>Batal</Button>
                    <Button type="primary">Perbaharui</Button>
                  </div>
                </Form.Item>
              </Col>
            </Row>
          </Form>
        </div>
      </Card>
      </>
    )
  }
}

export default AddEmployee